import React from 'react'
import { Route, Switch } from 'react-router'
import './App.css'

/* global mp */
class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      is_login: true,
      l_login: "", l_password: "",
      r_login: "", r_email: "", r_password: "", r_repeat_password: "", r_promocode: ""
    }
  }
  handleChange = (event) => {
    let name = event.target.name
    let value = event.target.value
    this.setState({ [name]: value })
  }
  switch = () => {
    this.setState({ is_login: !this.state.is_login })
  }
  login = () => {
    // авторизация
    // mp.trigger здесь
  }
  register = () => {
    // регистрация
    // mp.trigger здесь
  }
  render() {
    return (
      <Switch>
        <Route exact path="/login">
          <div className="App Auth">
            {this.state.is_login
              ?
              <div className="Login window">
                <div className="logotype" />
                <div className="title">Авторизация</div>
                <div className="controls">
                  <input
                    name="l_login"
                    value={this.state.l_login}
                    onChange={this.handleChange}
                    placeholder="Ваш логин" />
                  <input
                    type="password"
                    name="l_password"
                    value={this.state.l_password}
                    onChange={this.handleChange}
                    placeholder="Ваш пароль" />
                  <button onClick={this.login}>Вход</button>
                </div>
                <div className="footer">
                  <span>Нет аккаунта?</span>
                  <span onClick={this.switch}>Регистрация</span>
                </div>
              </div>
              :
              <div className="Register window">
                <div className="logotype" />
                <div className="title">Регистрация</div>
                <div className="controls">
                  <input
                    name="r_login"
                    value={this.state.r_login}
                    onChange={this.handleChange}
                    placeholder="Ваш логин" />
                  <input
                    name="r_email"
                    value={this.state.r_email}
                    onChange={this.handleChange}
                    placeholder="Ваш email" />
                  <input
                    type="password"
                    name="r_password"
                    value={this.state.r_password}
                    onChange={this.handleChange}
                    placeholder="Ваш пароль" />
                  <input
                    type="password"
                    name="r_repeat_password"
                    value={this.state.r_repeat_password}
                    onChange={this.handleChange}
                    placeholder="Повторите пароль" />
                  <input
                    name="r_promocode"
                    value={this.state.r_promocode}
                    onChange={this.handleChange}
                    placeholder="Промокод" />
                  <button onClick={this.register}>Зарегистрироваться</button>
                </div>
                <div className="footer">
                  <span onClick={this.switch}>Отмена</span>
                </div>
              </div>
            }
          </div>

        </Route>
      </Switch>
    )
  }
}

export default App